import { Card, Table } from "react-bootstrap";
import "../../assets/css/slide.scss";
import React, { Suspense, useState } from "react";
import ConnectButton from "../../layout/common/connectButton";
import AuthorInfo from "./authorInfo";
import NoData from "../../layout/common/noData";
import PopupModal from "../../layout/common/popup/popupModal";
import { getEventVoicesMore } from "../../../../../actions/analyticsNew";
import SectionHeading from "../../layout/common/sectionHeading";
import MultiPostMedia from "../../layout/common/multiPostMedia";

const AuthorpostsPopup = React.lazy(() =>
    import("../../layout/popup/authorpostsPopup")
);

const TopeventSekleton = React.lazy(() =>
    import("../../layout/skeleton/topeventSekleton")
);

const TopeventVoice = (props) => {

    const [show, setShow] = useState(false)

    const handlePopup = event => {
        setShow(event)
    }

    const { topeventVoices, top_event_loader, selectedWallFeed } = props;

    return (
        <>

            <Card className="sw-card-custom gutter-b card-shadow mb-5">
                <SectionHeading title={'Top Event Voices'} selectedWallFeed={selectedWallFeed} total={topeventVoices.total} handlePopup={handlePopup} />
                <Card.Body className="py-5 px-0">
                    <Table className="mb-0" responsive>
                        <thead>
                            <tr>
                                <th style={{ width: "10%" }}>
                                    S No.
                                </th>
                                <th style={{ width: "30%" }}>
                                    Name
                                </th>
                                <th style={{ width: "20%" }}>
                                    Followers
                                </th>
                                <th style={{ width: "20%" }}>
                                    Recent Posts
                                </th>
                                <th style={{ width: "20%" }}></th>
                            </tr>
                        </thead>
                        <tbody>
                            {top_event_loader ? <TopeventSekleton /> : topeventVoices && Object.keys(topeventVoices.data).length
                                ? topeventVoices.data.map((data, key) => {
                                    return (
                                        <tr className="border-top">
                                            <td>
                                                <strong>{key + 1}</strong>
                                            </td>
                                            <td>
                                                <AuthorInfo
                                                    picture={data.image}
                                                    authorUsername={data.author.username}
                                                    link={data.links}
                                                    authorName={data.author.name}
                                                    networkId={data.network.id}
                                                    isIcon={data.network.icon}
                                                />
                                            </td>
                                            <td>
                                                <strong>{data.postCount}</strong>
                                            </td>
                                            <td>
                                                <MultiPostMedia postsData={data.postsData} />
                                            </td>
                                            <td>
                                                <ConnectButton link={data.links} network={data.network.id} />
                                            </td>
                                        </tr>
                                    );
                                })
                                : <NoData status={1} col={5} />
                            }
                        </tbody>
                    </Table>
                </Card.Body>
            </Card>

            {topeventVoices.total > 5 ?
                <PopupModal size={'xl'} showHide={show} handlePopup={handlePopup} title={'Top Event Voices'}>
                    <Suspense fallback={<></>}>
                        <AuthorpostsPopup total={topeventVoices.total} moreData={getEventVoicesMore} {...props} />
                    </Suspense>
                </PopupModal> : null}
        </>
    );
};

export default React.memo(TopeventVoice);;
